
.App {
  text-align: center;
  font-family: 'Dosis', sans-serif;
  font-family: 'M PLUS 1 Code', sans-serif;
}

.about {
  background-image: url(./components/images/background-black-lines.jpg);
  filter: brightness(20%);
  filter: contrast(120%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.app-container {
  background-image: url(./components/images/prog-bckgrd.jpg);
   filter: brightness(20%);
   filter: contrast(120%);
  
   
}

body {
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
  background-image: url(./components/images/banner-dark-purple.jpg);
    filter: brightness(80%);
    filter: contrast(120%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  font-family: 'M PLUS 1 Code', sans-serif;
  font-weight: bolder;
  color:#1602f3;
  -webkit-text-stroke-width: 1%;
  -webkit-text-stroke-color: rgb(144, 120, 204);
  
}

h1 {
  color:#f3f3f3;
  -webkit-text-stroke-width: 1%;
  -webkit-text-stroke-color: rgb(0, 0, 0);
  font-family:fantasy;
  
}

.App-link {
  color: #ffffff;
}

.nav-tabs {
  display:flex;
  justify-content: space-evenly;
  color:#f3f3f3;
  -webkit-text-stroke-width: 1%;
  -webkit-text-stroke-color: rgb(144, 120, 204);
  border:2px solid rgb(79, 3, 160);
  box-shadow:
  inset 0 -3em 3em rgba(0,0,0,0.1),
        0 0  0 2px rgb(57, 57, 57),
        0.3em 0.3em 1em rgba(0,0,0,0.3);
}

.header-container {
  display:flex;
  justify-content: space-between;
  border:4px solid rgb(81, 0, 221);
  background-image: url(./components/images/background-black-lines.jpg);
  filter: brightness(40%);
   filter: contrast(80%);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
  color:rgb(255, 255, 255);
  border-radius: 3%;
  box-shadow: #c4c4c4;
}

.border-gradient {
  border: 5px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(to left, #743ad5, #d53a9d);
}







.box-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
  grid-gap: 1.5rem;
  justify-items: center;
  margin: 0;
  padding: 0;
  
}

.box-flex {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  margin: 1rem;
  text-align: center;
  font-size: 1.2rem;
  word-break: break-word;
  color: #00ceb6;
  border:2px solid #434343;
  
}

.box-flex-bottom-right {
  align-items: flex-end;
  justify-content: center;
}

.box-caption {
  position: absolute;
  max-width: 200px;
  margin: 20px;
  padding: .5rem;
  word-break: break-word;
  font-size: 1.25rem;
  color: #000000;
  font-family: 'M PLUS 1 Code', sans-serif;
}

.box-caption:hover {
  color: #c4c4c4;
  transition: all ease .5s;
}

#name {
  color:#e0d1f7;
 -webkit-text-stroke-width: 1%;
  -webkit-text-stroke-color: rgb(144, 120, 204);
  background-color: rgb(121, 119, 119);
}

#email {
  color:#e0d1f7;
 -webkit-text-stroke-width: 1%;
  -webkit-text-stroke-color: rgb(144, 120, 204);
  background-color: rgb(120, 120, 120);

}

.message textarea {
 min-width: 500px;
 color:#e0d1f7;
 -webkit-text-stroke-width: 1%;
  -webkit-text-stroke-color: rgb(144, 120, 204);
 background-color: black;
}

.portfolio {
  text-align: center;
  background-image: url(./components/images/background-purple-mountain.png);
  background-repeat: no-repeat;
  background-position: center;
   filter:saturate(120);
   filter: brightness(50%);
   filter: contrast(135%);
  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: soft-light;
  border-radius: 3%;
  border:4px solid #410096;
  color:rgb(2, 0, 72);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(192, 192, 194);
  min-height: 80vh;
  box-shadow:
       inset 0 -3em 3em rgba(0,0,0,0.1),
             0 0 0 4px rgb(57, 57, 57),
             0.3em 0.3em 1em rgba(0,0,0,0.3);
             font-size: x-large;
             font-weight: bolder;
             font-family: 'M PLUS 1 Code', sans-serif;
}

img{
 max-width: 200;
 max-height: 200;
}

a {
  color:#ae4cf4;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(0, 0, 0);
}

.icon-container {
  display:flex;
  justify-content: center;
  height:1%vh;
  width:1%vw;
  background-image: url(./components/images/banner-purple.jpg);
  justify-content: space-evenly;
  color:#7900fa;
  
}

.icon {
  display:flex;
  justify-content: space-between;
  margin-top: 20px;
  
}

.icon:hover {
  fill: #1602f3;
  transition: all ease .8s;
}

@media (max-width: 480px) {
  .portfolio {
    font-size: 16px;
  }
}